// Шрифты + normalize ↓
@import "fonts+normalize";
// Переменные ↓
@import "vars";
// Миксины ↓
@import "mixins";
// Плагины и библиотеки CSS/SASS ↓
@import "libs";
// Подключаем спрайты (раскомментируй @import при появлении PNG спрайтов) ↓
@import "sprite";

/* 1. MAIN ================== */

.main {
	position: relative;
	overflow: hidden;
	min-height: 100%;
	width: 100%;
	min-width: 320px;
	margin: 0 auto; }

.wrapper {
	position: relative;
	max-width: 1200px;
	min-width: 320px;
	padding: 0 15px;
	margin-right: auto;
	margin-left: auto;
	&--for-headline {
		padding-right: 0; } }

.img-responsive {
	display: block;
	max-width: 100%;
	height: auto; }

.vh {
	visibility: hidden; }


#my-menu:not( .mm-menu ) {
	display: none; }
/* Arrow TOP ================== */

.arrow-top {
	position: fixed;
	right: 15px;
	bottom: -100px;
	width: 60px;
	height: 60px;
	z-index: 9999;
	background-color: $main;
	@include br(5px);
	border: 1px solid $accent;
	opacity: 0;
	@include tr(0.7s);
	&:hover {
		background-color: lighten($main, 10%); }
	&__icon {
		display: block;
		margin: 8px auto 0;
		width: 40px;
		height: 40px; }
	&.top-hidden {
		bottom: 15px;
		opacity: 1;
		visibility: visible;
		transition-delay: 0; } }
.tr {
	@include tr(0s); }

/* Arrow TOP END ================== */

/* PRELOADER ================== */

.loaderArea {
	width: 100vw;
	background: #F2F1EF;
	overflow: hidden;
	position: fixed;
	left: 0;
	top: 0;
	right: 0;
	bottom: 0;
	z-index: 99999999; }

$animation-duration: 1.3s;

.loader {
	position: absolute;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);
	width: 50px;
	height: 10px;
	background: #3498DB;
	border-radius: 5px;
	&:before, &:after {
		position: absolute;
		display: block;
		content: "";
		animation: load $animation-duration ease-in-out infinite;
		height: 10px;
		border-radius: 5px; }
	&:before {
		top: -20px;
		left: 10px;
		width: 40px;
		background: #EF4836; }
	&:after {
		bottom: -20px;
		width: 35px;
		background: #F5AB35; } }

@keyframes load {
	0% {
		transform: translateX(40px); }
	50% {
		transform: translateX(-20px); }
	100% {
		transform: translateX(40px); } }

/* PRELOADER END ================== */

/* Стилизация полосы прокрутки ================== */

::-webkit-scrollbar-button {
	background-image: url ('');
	background-repeat: no-repeat;
	width: 6px;
	height: 0px; }

::-webkit-scrollbar-track {
	background-color: $main; // Цвет дорожки, по которой двигается бегунок прокрутки
	box-shadow: 0px 0px 3px #000 inset; }

::-webkit-scrollbar-thumb {
	border-radius: 0px; // Закругление бегунка
	background-color: $accent; // Цвет бегунка
	background-image: url("../img/icon_for_scrol.png");
	background-position: center;
	background-repeat: no-repeat; }

::-webkit-scrollbar-thumb:hover {
	background-color: lighten($accent, 10%); } // Цвет бегунка при наведении на него курсора

::-webkit-resizer {
	background-image: url('');
	background-repeat: no-repeat;
	width: 7px;
	height: 0px; }

::-webkit-scrollbar {
	width: 15px; } // Основная ширина полосы прокрутки

// For Internet Explorer
body {
	scrollbar-face-color: $accent;
	scrollbar-track-color: $main;
	scrollbar-shadow-color: $accent;
	scrollbar-arrow-color: $accent; }

/* 2. BUTTON ================== */

.btn, a.btn {
	display: inline-block;
	border: none;
	color: #fff;
	text-decoration: none;
	background-color: $accent;
	padding: 6px 10px 6px 10px;
	font-size: 14px;
	text-transform: uppercase;
	border-radius: 0px;
	text-align: center;
	position: relative;
	outline: none;
	transition: background-color .1s ease;
	&:focus, &:hover {
		text-decoration: none;
		color: #fff; }
	&:hover {
		background-color: lighten($accent, 10%);
		&::after {
			opacity: .22; } }
	&:active {
		background-color: darken($accent, 5%);
		&::after {
			opacity: .32; } } }

input[type="submit"].btn, button.btn {
	padding-top: 0; }

/* 3. FORM ================== */

input[type="text"],
input[type="password"],
input[type="tel"],
input[type="email"],
textarea {
	height: 38px;
	padding: 10px;
	font-family: "Oswald";
	color: #656565;
	background: rgba(32,32,32,0.5);
	border: 1px solid #7d7d7d;
	line-height: 12px;
	&.form-control {
		display: block;
		width: 100%; } }

textarea {
	resize: none;
	height: auto;
	min-height: 100px;
	padding-top: 10px; }

label {
	display: block; }

/* 4. HEADER ================== */

