	> li:nth-child(3) {
		> a {
			&::before {
				content: '\f0c0';
				font-family: fontawesome; } } }
	> li:nth-child(4) {
		> a {
			&::before {
				content: '\f03e';
				font-family: fontawesome; } } }
	> li:nth-child(5) {
		> a {
			&::before {
				content: '\f155';
				font-family: fontawesome; } } }
	> li:nth-child(6) {
		> a {
			&::before {
				content: '\f2b5';
				font-family: fontawesome; } } }
	> li:nth-child(7) {
		> a {
			&::before {
